@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
p{
  margin:0 !important;
}


h1{
  margin:0 !important;
}
html{
  margin:0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul{
  margin:0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#topBanner{
  width: 100%;
  height:35vh;
  display:flex;
  flex-direction: column;
  background-color: #D0EBF6;
  color: #D39052;
  align-items: flex-start;
  justify-content: flex-start;
}
#topBanner h1{
  padding-left: 13vw;
  padding-top:13vh;
}
#descriptionBanner{
  width:100vw;
  height:55vh;
  background-color:#D39052;
  
}

#bottomBanner{
  width:100vw;
  height:10vh;
  background-color:#C6772D ;
  display: table;

}
#nameWrapper{

  margin-right:50vw;
  color: #D39052;
  padding-top: 14vh;
  align-items: flex-start;
  justify-content: flex-start;
}
#nameWrapper #title{
  padding-left:.2em;

}
#typing{
  transform: scaleX(-1);
  position: absolute;
  padding-right: 80vw;
  filter: blur(10px) brightness(-100);
  -webkit-filter: blur(10px) brightness(-100);

}

#topBanner h1{
  font-family: 'Space Mono', monospace;
  font-size: 2.5vw;
  font-weight: 700;

}
#descriptionBanner h1{
  
  font-family: 'Space Mono', monospace;
  font-size: 2.5vw;
  padding-right:62.5vw;
  padding-top:2%;
  color:#D0EBF6;
  font-weight: 700;
}
#paragraph{

  width: 100vw;
  text-align: left;
}
#paragraph p{
  color: #D0EBF6;
  padding-right:14%;
  padding-left:12.5%;
  font-size: 155%;
  font-family: 'Space Mono', monospace;
}
.cell{
  display: table-cell;
  width: 33vw;
  font-family: 'Space Mono', monospace;
  color: #D0EBF6;
  text-align:middle;
  vertical-align: middle;
}

a {
  color: #FFF;
}
a:hover {
   color: #FFF;
   text-decoration: none;
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

